import React from 'react'
import { useState } from 'react'
import KeepHeader from './KeepHeader'
import KeepFooter from './KeepFooter'
import CreateNote from './CreateNote'
import Note from './Note'
import './Keep.css'
const Keep = () => {
    const [addItem, setAddItem]=useState([])
    const addNote = (note) => {
        
        setAddItem((prevData)=>{
            return [...prevData,note]
        })
        
    };
    const onDelete=(id)=>{
        setAddItem((oldData)=>
        oldData.filter((cuurData,index)=>{
                return index!==id;
        })
        )
    }
    return (
        <>
        <KeepHeader/>
        <CreateNote 
            passNote={addNote}
        />
        
       { addItem.map((val,index)=>{
            return <Note
                    key={index}
                    id={index}
                    title={val.title}
                    content={val.content}
                    deleteItem={onDelete}
                    />
        })}
        <KeepFooter/>
        </>
    )
}

export default Keep
